import React from "react";
const CardRow = ({ cardAmount, drawProbability, cardName }) => {
    return (React.createElement("div", null,
        React.createElement("span", null,
            " ",
            drawProbability,
            " "),
        React.createElement("span", null,
            " ",
            cardName,
            " "),
        React.createElement("span", null,
            " ",
            cardAmount,
            " ")));
};
export default CardRow;
