import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import { getCountsFromArray } from "../utils/utilityFunctions";
import CardRow from "./CardRow";
const LibraryView = () => {
    const [libraryMap, setLibraryMap] = useState(new Map());
    const pd = useSelector((state) => state.options.playerDeck);
    useEffect(() => {
        setLibraryMap(getCountsFromArray(pd.library));
    }, [pd]);
    return (React.createElement(React.Fragment, null,
        React.createElement("div", null,
            "Library ",
            pd.library.length),
        React.createElement("br", null),
        Array.from(libraryMap.keys()).map((card, idx) => {
            return (React.createElement(CardRow, { key: idx, drawProbability: "", cardName: card, cardAmount: libraryMap.get(card) }));
        })));
};
export default LibraryView;
