import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import { getCountsFromArray } from "../utils/utilityFunctions";
import CardRow from "./CardRow";
const TrashView = () => {
    const [trashMap, setTrashMap] = useState(new Map());
    const pd = useSelector((state) => state.options.playerDeck);
    useEffect(() => {
        setTrashMap(getCountsFromArray(pd.trash));
    }, [pd]);
    return (React.createElement(React.Fragment, null,
        React.createElement("div", null,
            "Trash ",
            pd.trash.length),
        React.createElement("br", null),
        Array.from(trashMap.keys()).map((card, idx) => {
            return (React.createElement(CardRow, { key: idx, drawProbability: "", cardName: card, cardAmount: trashMap.get(card) }));
        })));
};
export default TrashView;
