import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import { getCountsFromArray } from "../utils/utilityFunctions";
import CardRow from "./CardRow";
const DecklistView = () => {
    const [listMap, setListMap] = useState(new Map());
    const pd = useSelector((state) => state.options.playerDeck);
    useEffect(() => {
        setListMap(getCountsFromArray(pd.entireDeck));
    }, [pd]);
    return (React.createElement(React.Fragment, null,
        React.createElement("div", null,
            "Full Decklist ",
            pd.entireDeck.length),
        React.createElement("br", null),
        Array.from(listMap.keys()).map((card, idx) => {
            return (React.createElement(CardRow, { key: idx, drawProbability: "", cardName: card, cardAmount: listMap.get(card) }));
        })));
};
export default DecklistView;
