import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import { getCountsFromArray } from "../utils/utilityFunctions";
import CardRow from "./CardRow";
const DiscardFrame = () => {
    const [discardMap, setDiscardMap] = useState(new Map());
    const pd = useSelector((state) => state.options.playerDeck);
    useEffect(() => {
        setDiscardMap(getCountsFromArray(pd.graveyard));
    }, [pd]);
    return (React.createElement(React.Fragment, null,
        React.createElement("div", null,
            "Discard Pile ",
            pd.graveyard.length),
        React.createElement("br", null),
        Array.from(discardMap.keys()).map((card, idx) => {
            return (React.createElement(CardRow, { key: idx, drawProbability: "", cardName: card, cardAmount: discardMap.get(card) }));
        })));
};
export default DiscardFrame;
