import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import { getCountsFromArray } from "../utils/utilityFunctions";
import CardRow from "./CardRow";
const InPlayView = () => {
    const [inPlayMap, setInPlayMap] = useState(new Map());
    const pd = useSelector((state) => state.options.playerDeck);
    useEffect(() => {
        setInPlayMap(getCountsFromArray(pd.inPlay));
    }, [pd]);
    return (React.createElement(React.Fragment, null,
        React.createElement("div", null,
            "InPlay ",
            pd.inPlay.length),
        React.createElement("br", null),
        Array.from(inPlayMap.keys()).map((card, idx) => {
            return (React.createElement(CardRow, { key: idx, drawProbability: "", cardName: card, cardAmount: inPlayMap.get(card) }));
        })));
};
export default InPlayView;
