import { createSlice } from "@reduxjs/toolkit";
import { Deck } from "../../model/deck";
const initialState = {
    playerDeck: JSON.parse(JSON.stringify(new Deck("emptyPlayer", "ep", ["empty Kingdom"]))),
    opponentDeck: JSON.parse(JSON.stringify(new Deck("emptyOpponent", "eo", ["empty Kingdom"]))),
};
export const optionsSlice = createSlice({
    name: "options",
    initialState,
    reducers: {
        setPlayerDeck: (state, action) => {
            state.playerDeck = action.payload;
        },
        setOpponentDeck: (state, action) => {
            state.opponentDeck = action.payload;
        },
    },
});
export const { setPlayerDeck, setOpponentDeck } = optionsSlice.actions;
export const selectOptions = (state) => state.options;
export default optionsSlice.reducer;
