import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import { getCountsFromArray, } from "../utils/utilityFunctions";
import CardRow from "./CardRow";
const HandView = () => {
    const [handMap, setHandMap] = useState(new Map());
    const pd = useSelector((state) => state.options.playerDeck);
    useEffect(() => {
        setHandMap(getCountsFromArray(pd.hand));
    }, [pd]);
    return (React.createElement(React.Fragment, null,
        React.createElement("div", null,
            "Hand ",
            pd.hand.length),
        React.createElement("br", null),
        Array.from(handMap.keys()).map((card, idx) => {
            return (React.createElement(CardRow, { key: idx, drawProbability: "", cardName: card, cardAmount: handMap.get(card) }));
        })));
};
export default HandView;
